<script>
// @ is an alias to /src

import Home from "@/components/Home.vue";
import db from "@/firebaseConfig.js";
export default {
  name: "Home",
  components: {
    Home,
  },
  data() {
    return {
      categories: [],
    };
  },
  created() {
    db.collection("categories")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const category = {
            id: doc.id,
            cat_id: doc.data().cat_id,
            cat_name: doc.data().name,
            items: [],
          };
          db.collection("categories")
            .doc(doc.id)
            .collection("items")
            .get()
            .then((querySnapshot) => {
              querySnapshot.forEach((doc) => {
                const item = {
                  id: doc.id,
                  item_id: doc.data().item_id,
                  item_name: doc.data().item_name,
                  item_price: doc.data().item_price,
                };
                category.items.push(item);
              });
            });
          this.categories.push(category);
        });
      });
  },
};
</script>
